import axios from 'axios';
import useAuthStore, { selectToken } from '../store/authStore';

// Crear instancia base de axios
const api = axios.create({
  baseURL: process.env.REACT_APP_API_URL || 'http://localhost:8000/api',
  headers: {
    'Content-Type': 'application/json',
  },
  timeout: 10000, // 10 segundos de timeout
});

// Cache para tokens
let tokenCache = null;
let tokenExpiry = null;

// Funcin para obtener el token con cach
const getToken = () => {
  const now = Date.now();
  if (tokenCache && tokenExpiry && now < tokenExpiry) {
    return tokenCache;
  }
  tokenCache = useAuthStore.getState().token;
  tokenExpiry = now + 60000; // Cache por 1 minuto
  return tokenCache;
};

// Interceptor de solicitudes
api.interceptors.request.use(
  (config) => {
    const token = getToken();
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Interceptor de respuestas
api.interceptors.response.use(
  (response) => response,
  async (error) => {
    const originalRequest = error.config;

    // Si el error es 401 y no hemos intentado refrescar el token
    if (error.response?.status === 401 && !originalRequest._retry) {
      originalRequest._retry = true;

      try {
        // Intentar refrescar el token
        const refreshToken = localStorage.getItem('refreshToken');
        if (refreshToken) {
          const response = await axios.post(`${api.defaults.baseURL}/auth/refresh/`, {
            refresh: refreshToken,
          });

          const { access } = response.data;
          useAuthStore.getState().setToken(access);
          tokenCache = access;
          tokenExpiry = Date.now() + 60000;

          // Reintentar la solicitud original
          originalRequest.headers.Authorization = `Bearer ${access}`;
          return api(originalRequest);
        }
      } catch (refreshError) {
        // Si falla el refresh, hacer logout
        useAuthStore.getState().logout();
        window.location.href = '/login';
        return Promise.reject(refreshError);
      }
    }

    return Promise.reject(error);
  }
);

// Funcin para limpiar el cach
export const clearApiCache = () => {
  tokenCache = null;
  tokenExpiry = null;
};

export default api; 